const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: ''
		};
	},
	onLoad(e) {
		this.imgUrl = helper.imgUrl
		uni.setNavigationBarTitle({
			title: e.title
		});
	},
	methods: {
		// 发布
		getPost(){
			uni.navigateTo({
				url: `/pages/experience`
			});
		},
		// 置顶加载更多
		getMore(){
			this.page = '1'
		}
	}
};
